
.main-menu--icon {
  fill: #777;
  -webkit-transition: fill 100ms ease-in-out;
  transition: fill 100ms ease-in-out;
}
.main-menu--button:hover .main-menu--icon {
  fill: #000;
}
.router-link-active > .main-menu--button .main-menu--icon {
  fill: #000;
}



