/* PAGE */

html, body {
  background-color: #F0F0F0;
  font-family: 'Asap Condensed', sans-serif;
  font-size: 14px;
  height: 100vh;
  margin: 0;
  padding: 0;
  width: 100%;
}

* {
  box-sizing: border-box;
}


/* FONTS */

a, button, div, input, optgroup, select, span, textarea {
  font-family: 'Asap Condensed', sans-serif;
}

blockquote, li, p {
  font-family: 'Khula', sans-serif;
  line-height: 1.5em;
}

em {
  font-style: italic;
}

strong {
  font-weight: bold;
}

/* TAGS */

a {
  cursor: pointer;
  font-size: 14px;
  line-height: 21px;
  text-decoration: underline;
}

button {
  background-color: transparent;
  border: 1px solid #CCC;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  padding: 6px 10px;
}

button.button-green {
  background-color: #8bc34a;
  border: none;
  color: #FFF;
  fill: #FFF;
}

button.button-icon {
  border: none;
  padding: 0;
}

button.button-link {
  border: none;
  color: #000;
  text-decoration: underline;
}

button.button-link[disabled] {
  background-color: transparent;
  color: #444;
}

.button-link-icon {
  margin-right: 4px;
}

button.button-red {
  background-color: #e53935 !important;
  border: none;
  color: #FFF !important;
  fill: #FFF;
}

.button-tab {
  align-items: center;
  background-color: transparent;
  border: 1px solid #CCC;
  border-bottom: none;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-color: #CCC;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  display: flex;
  font-size: 14px;
  margin-right: 1px;
  padding: 6px 10px;
  transition: background-color 100ms, border-color 100ms;
}

.button-tab.active {
  background-color: #FFF;
  border-color: #e8cc84;
}

button[disabled] {
  background-color: #888;
  cursor: default;
}

h1, h2, h3, h4, h5 {
  line-height: 1.2em;
}

h1, h2, h3 {
  margin-bottom: 6px;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 28px;
}

h3 {
  font-size: 22px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 15px;
}

hr {
  border: none;
  border-bottom: 1px solid #CCC;
  margin: 20px 5px;
}

hr.vert {
  border: none;
  border-right: 1px solid #CCC;
  display: inline-block;
  height: 20px;
  width: 1px;
}

hr.vert-flex {
  border: non;
  border-right: 1px solid #CCC;
  display: inline;
  margin: 0 10px;
}

input {
  border: 1px solid #e8cc84;
  border-radius: 2px;
  padding: 4px;
}

input[type="checkbox"] {
  margin-right: 4px;
}

input[type="file"] {
  cursor: pointer;
}

li {
  list-style-type: disc;
  margin-bottom: 6px;
  margin-left: 18px;
}

p {
  margin-bottom: 6px;
}

select {
  background-color: transparent;
  border: none;
}

textarea {
  border: 1px solid #CCC;
}

/* UTILITY CLASSES */

.u-center-all {
  align-items: center;
  display: flex;
  justify-content: center;
}

/* COMMON CLASSES */

.help {
  font-size: 14px;
  margin-left: 16px;
  text-align: left;
}

.help li {
  text-align: left;
}

.help p {
  margin-bottom: 10px;
}

.help-icon {
  border: none;
  height: 20px;
  padding: 0;
  transition: opacity 100ms;
  width: 20px;
}

.help-icon--svg {
  fill: #444;
}



